body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  text-align: center;
}

.jumbotron {
  background-color: white;
}

.bgbox {
  background-color: LightGrey;
  height: 200px;
  line-height: 160px;
  padding: 10px;
  margin-bottom: 20px;
}

.bgbox h3 {
  vertical-align: middle;
  display: inline-block;
}

.clickable:hover {
  cursor: pointer;
}

.questionbox {
  background-color: LightGrey;
  padding: 20px 0px;
  margin-bottom: 20px;
}

input[type="text"] {
  border-style: solid;
  border-width: 1px;
  margin: 5px;
  padding: 5px;
}


